import * as _ from './underscore.js'

class LiquidError extends Error {
  constructor (message) {
    super(message)
    this.name = this.constructor.name
    if (typeof Error.captureStackTrace === 'function') {
      Error.captureStackTrace(this, this.constructor)
    } else {
      this.stack = (new Error(message)).stack
    }
  }
}

class TemplateError extends LiquidError {
  constructor (err, token) {
    super()
    this.input = token.input
    this.line = token.line
    this.file = token.file
    this.message = mkMessage(err.message, token)
    const context = mkContext(token.input, token.line)
    this.stack = context +
      '\n' + (this.stack || this.message) +
        (err.stack ? '\nFrom ' + err.stack : '')
  }
}

export class TokenizationError extends TemplateError {
  constructor (message, token) {
    super({message: message}, token)
  }
}

export class ParseError extends TemplateError {
  constructor (e, token) {
    super(e, token)
    _.assign(this, e)
    this.originalError = e
  }
}

export class RenderError extends TemplateError {
  constructor (e, tpl) {
    // return the original render error
    if (e instanceof RenderError) {
      return e
    }
    super(e, tpl.token)
    _.assign(this, e)
    this.originalError = e
  }
}

export class RenderBreakError extends LiquidError {
  constructor (message) {
    super(message + '')
  }
}

export class AssertionError extends LiquidError {
  constructor (message) {
    super(message + '')
  }
}

function mkContext (input, line) {
  const lines = input.split('\n')
  const begin = Math.max(line - 2, 1)
  const end = Math.min(line + 3, lines.length)

  const context = _
    .range(begin, end + 1)
    .map(l => [
      (l === line) ? '>> ' : '   ',
      align(l, end),
      '| ',
      lines[l - 1]
    ].join(''))
    .join('\n')

  return context
}

function align (n, max) {
  const length = (max + '').length
  const str = n + ''
  const blank = Array(length - str.length).join(' ')
  return blank + str
}

function mkMessage (msg, token) {
  msg = msg || ''
  if (token.file) {
    msg += ', file:' + token.file
  }
  if (token.line) {
    msg += ', line:' + token.line
  }
  return msg
}
